body {
  position: relative;
  font-family: "Open Sans", sans-serif;
  font-size: 1.25rem;
  font-weight: normal;
  letter-spacing: 0.5px;
  color: #000000;
  overflow-x: hidden;
  overflow-y: scroll; }
