html {
  line-height: 1.3; }

.container {
  transition: max-width 0.3s ease-in; }

.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  min-height: 87vh; }

main {
  padding-top: 90px; }

header {
